'use client';

import { useEffect, useState } from 'react';
import { useRouter } from 'next/navigation';
import { Trip } from '@/types/trip';
import Map from '@/components/Map';
import { format } from 'date-fns';

interface TripDetailsProps {
  id: string;
}

export default function TripDetails({ id }: TripDetailsProps) {
  const [trip, setTrip] = useState<Trip | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState('');
  const router = useRouter();

  useEffect(() => {
    const fetchTrip = async () => {
      try {
        const accessToken = localStorage.getItem('accessToken');
        if (!accessToken) {
          // Redirect to login page with return URL
          router.push(`/auth/login?returnUrl=/trips/${id}`);
          return;
        }

        const response = await fetch(`http://localhost:8000/api/trips/${id}/`, {
          method: 'GET',
          headers: {
            'Authorization': `Bearer ${accessToken}`,
            'Content-Type': 'application/json',
            'Accept': 'application/json',
          },
          credentials: 'include',
        });

        if (response.status === 401) {
          // Token expired or invalid
          localStorage.removeItem('accessToken');
          router.push(`/auth/login?returnUrl=/trips/${id}`);
          return;
        }

        if (!response.ok) {
          const errorData = await response.json().catch(() => null);
          console.error('Response error:', {
            status: response.status,
            statusText: response.statusText,
            errorData
          });
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        
        const data = await response.json();
        console.log('Trip data:', data);
        setTrip(data);
      } catch (err) {
        console.error('Fetch error:', err);
        setError(err instanceof Error ? err.message : 'Error loading trip details');
      } finally {
        setLoading(false);
      }
    };

    fetchTrip();
  }, [id, router]);

  const handleStatusChange = async (newStatus: string) => {
    try {
      const accessToken = localStorage.getItem('accessToken');
      if (!accessToken) {
        router.push(`/auth/login?returnUrl=/trips/${id}`);
        return;
      }

      const response = await fetch(`http://localhost:8000/api/trips/${id}/`, {
        method: 'PATCH',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${accessToken}`,
          'Accept': 'application/json',
        },
        credentials: 'include',
        body: JSON.stringify({ status: newStatus }),
      });

      if (response.status === 401) {
        localStorage.removeItem('accessToken');
        router.push(`/auth/login?returnUrl=/trips/${id}`);
        return;
      }

      if (!response.ok) {
        const errorData = await response.json().catch(() => null);
        console.error('Status update error:', {
          status: response.status,
          statusText: response.statusText,
          errorData
        });
        throw new Error(`Failed to update trip status: ${response.statusText}`);
      }

      const updatedTrip = await response.json();
      setTrip(updatedTrip);
    } catch (err) {
      console.error('Status update error:', err);
      setError(err instanceof Error ? err.message : 'Error updating trip status');
    }
  };

  // Don't show loading state if we're redirecting to login
  if (!localStorage.getItem('accessToken')) {
    return null;
  }

  if (loading) return (
    <div className="flex justify-center items-center min-h-screen">
      <div className="text-xl">Loading...</div>
    </div>
  );
  
  if (error) return (
    <div className="flex justify-center items-center min-h-screen">
      <div className="text-xl text-red-600">{error}</div>
    </div>
  );
  
  if (!trip) return (
    <div className="flex justify-center items-center min-h-screen">
      <div className="text-xl">Trip not found</div>
    </div>
  );

  return (
    <div className="container mx-auto px-4 py-8">
      <div className="bg-white rounded-lg shadow-lg overflow-hidden">
        {/* Header */}
        <div className="p-6 border-b">
          <div className="flex justify-between items-center">
            <h1 className="text-2xl font-bold">
              {trip.departure_location} → {trip.destination_location}
            </h1>
            <span className={`px-4 py-2 rounded-full text-sm font-semibold
              ${trip.status === 'active' ? 'bg-green-100 text-green-800' :
                trip.status === 'completed' ? 'bg-blue-100 text-blue-800' :
                trip.status === 'cancelled' ? 'bg-red-100 text-red-800' :
                'bg-gray-100 text-gray-800'}`}>
              {trip.status.charAt(0).toUpperCase() + trip.status.slice(1)}
            </span>
          </div>
        </div>

        {/* Map */}
        <div className="h-64 w-full">
          <Map
            departureCoords={trip.departure_coords.split(',').map(Number) as [number, number]}
            destinationCoords={trip.destination_coords.split(',').map(Number) as [number, number]}
            onMapClick={() => {}}
            onMarkerDrag={() => {}}
          />
        </div>

        {/* Trip Details */}
        <div className="p-6 grid grid-cols-1 md:grid-cols-2 gap-6">
          <div>
            <h2 className="text-xl font-semibold mb-4">Trip Details</h2>
            <div className="space-y-3">
              <p><span className="font-medium">Date:</span> {format(new Date(trip.date), 'PPP')}</p>
              <p><span className="font-medium">Time:</span> {trip.time}</p>
              <p><span className="font-medium">Price:</span> ${trip.price}</p>
              <p><span className="font-medium">Available Seats:</span> {trip.available_seats}</p>
              <p><span className="font-medium">Description:</span> {trip.description}</p>
            </div>
          </div>

          <div>
            <h2 className="text-xl font-semibold mb-4">Driver Information</h2>
            <div className="space-y-3">
              <p><span className="font-medium">Name:</span> {trip.driver.first_name} {trip.driver.last_name}</p>
              <p><span className="font-medium">Rating:</span> {trip.driver.driver_rating} ⭐</p>
              <p><span className="font-medium">Phone:</span> {trip.driver.phone_number}</p>
            </div>
          </div>
        </div>

        {/* Actions */}
        <div className="p-6 border-t bg-gray-50">
          <div className="flex justify-end space-x-4">
            <button
              onClick={() => router.push(`/trips/${id}/edit`)}
              className="px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700"
            >
              Edit Trip
            </button>
            {trip.status === 'active' && (
              <>
                <button
                  onClick={() => handleStatusChange('completed')}
                  className="px-4 py-2 bg-green-600 text-white rounded hover:bg-green-700"
                >
                  Complete Trip
                </button>
                <button
                  onClick={() => handleStatusChange('cancelled')}
                  className="px-4 py-2 bg-red-600 text-white rounded hover:bg-red-700"
                >
                  Cancel Trip
                </button>
              </>
            )}
          </div>
        </div>
      </div>
    </div>
  );
} 